import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import upLoad from '@/components/common/upload.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'

export default {
	data() {
		return {
			isBack: false,
			privacy: '所有人可见',
			pri_items: ['所有人可见', '仅好友可见', '仅自己可见'],
			text: '',
			imgList: [],
			showPopup: true
		};
	},
	components: {
		uniNavBar,
		upLoad,
		uniPopup
	},
	onBackPress() {
		if (!this.text && this.imgList.length < 1) return;
		if (!this.isBack) {
			this.save()
			return true;
		}
	},
	methods: {
		save () {
			uni.showModal({
				content: '是否保存为草稿',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if (res.confirm) {
						console.log(1);
					} else {
						console.log(2);
					}
					
					this.isBack = true
					uni.navigateBack({
						delta: 1
					})
				}
			});
		},
		back () {
			// console.log(1);
			uni.navigateBack({
				delta: 1
			})
		},
		submit () {
			console.log(this.imgList);
		},
		changeLook () {
			uni.showActionSheet({
				itemList: this.pri_items,
				success: (res) => {
					this.privacy = this.pri_items[res.tapIndex]
				}
			});
		},
		upload (arr) {
			this.imgList = arr
		},
		togglePopup () {
			this.showPopup = false
		}
	}
}
